.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header > h2 {
  color: white;
  font-size: calc(10px + 5vmin);
  /* #9da8a3; */
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 20px;
}
.card-image {
  flex: 0 0 auto;
  width: 100px; /* Adjusted width */
  height: 100px; /* Adjusted height */
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
}
.card-image img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.card-content {
  flex: 1 1 auto;
  padding: 20px;
}
.card-header {
  font-size: 24px;
  margin-bottom: 10px;
}
.card-sub-header {
  font-size: 18px;
  color: #666;
  margin-bottom: 5px;
}
.card-paragraph {
  font-size: 16px;
  margin-bottom: 10px;
}
